

// ===================
// Font and color variables
// ===================

// Fonts
$sans-serif:  sans-serif;
$font-light: 300;
$font-bold: 700;

// Colors
$black: #353535;
$gray: #4f4f4f;
$medium-gray: #737373;
$light-gray: #c4c4c4;
$orange: #f96855;

// Semantic color names
$accent: $orange;

$body-background: $black;
$menu-background: $gray;

$main-font-color: $light-gray;
$menu-active-color: $light-gray;
$menu-font-color: $medium-gray;
$link-color: $accent;



// ===================
// General styles
// ===================

* { box-sizing: border-box; }



// ===================
// Navigation
// ===================

.sidebar {
  background: $menu-background;
  flex: 1 1 20%;
  min-width: 300px;
  padding: 3em;
}

.logo {
  text-transform: uppercase;
  margin-bottom: 2em;
  
  @media (min-width: 1500px) {
    margin-bottom: 5em;
  }
  
  span {
    font-weight: $font-bold;
  }
}

.nav-item {
  color: $menu-font-color;
  text-decoration: none;
  font-size: 1.7rem;
  display: inline-block;
  margin-right: 2em;
  @media (min-width: 1500px) {
    display: block;
    margin: 7vh 0;
  }
  
  // &.active {
  //   color: $menu-active-color;
  // }
}



// ===================
// Main Content area
// ===================

.main-content {
  padding: 10%;
  flex: 1 1 100%;
  min-height: 100vh;
  width: 100vw;
  left: 0px;

}



// ===================
// Portfolio
// ===================

.portfolio {
  display: grid;
  height: 100%;
  width: cover;
  grid-template-rows: repeat(8, 1fr);
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 5px;
}

.portfolio-item {
  background: $accent;
  color: $light-gray;
  font-weight: $font-bold;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-size: 2.2em;
  background-size: cover;
  background-position: top left;
  transition: all ease-in-out 300ms;
  position: relative;
  z-index: 0;
  width: cover;
  
  &.small {
    grid-row: span 1;
    grid-column: span 1;
  }
  
  &.medium {
    grid-row: span 2;
    grid-column: span 2;
  }
  
  &.large {
    grid-row: span 3;
    grid-column: span 3;
  }
  
  &.tall {
    grid-row: span 3;
    grid-column: span 2;
  }
  
  &.wide {
    grid-row: span 2;
    grid-column: span 3;
  }
    
  &:hover {
   
    h1{display: none;}

  }
 
}

.one{background-image: url('../../images/image1.png');height:50vh;}
.two{background-image: url('../../images/image2.png');}
.three{background-image: url('../../images/image3.png');}
.two,.one,.three,.four ,.five,.six{
  &::before {
    content: '';
    display: block;
    background: $black;
    opacity: 0.8;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transition: all ease-in-out 300ms;
    z-index: -1;
  }
  
  &:hover::before {
    opacity: 0.1;
    transition: transform .5s ease;
 
  }

}

